:root{
    --primary:#46C2FF;
}

.btn{
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: var(--primary);
    color: #fff;
    border: 1px solid  var(--primary);
}

.btn--outline{
    background: #BD11FA;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid  var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 20px; 
    font-size:20px;
}

.btn--large {
    padding: 12px 26px; 
    font-size:20px;
}

.btn--medium:hover,
.btn--large:hover{
    background: #fff;
    color: #46C2FF;
    transition: all 0.3s ease-out;
}